@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.Header__title{padding:1em;border:1px solid black}

.ModeSelector{display:flex;justify-content:space-between;width:100%}.ModeSelector__select{flex-grow:1;font-size:1.1em;text-transform:capitalize}.ModeSelector__button{margin-left:10px;padding:0.2em 0.5em;font-size:1.2em;color:white;text-transform:uppercase;border:3px solid blue;border-radius:10px;background-color:blue;transition:all, 0.3s;cursor:pointer}.ModeSelector__button:hover{color:blue;background-color:white}

.Board{position:relative;width:100%;padding-bottom:100%;display:grid;grid-gap:1px 1px;gap:1px 1px;border:2px solid black;background-color:white}.Board__box{width:100%;padding-bottom:100%;background-color:white}.Board__box:hover{background-color:blue}

.Tracker__title{font-size:24px;margin-top:0.15em;padding-left:10px;margin-bottom:1em;text-align:start}.Tracker__log{display:grid;align-items:stretch;width:100%;text-align:start}.Tracker__entry{margin-top:0;margin-bottom:8px;padding:10px;background-color:beige}

html{font-family:'Roboto', sans-serif}.App__container{max-width:800px;padding:0 20px;margin:0 auto;text-align:center}.App__main{display:grid;grid-template-columns:3fr 1fr;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}@media (max-width: 600px){.App__main{grid-template-columns:1fr}}.App__content{justify-self:stretch}.App__loading-error{padding:1em 0;color:red;text-align:start}.App__selector{margin-bottom:20px}.App__sidebar{padding:0;width:100%;justify-self:start;align-self:start}@media (max-width: 600px){.App__sidebar{margin-top:1em}}

